  outline: none;
}

@media (max-width: 1024px) {
  .partenter__slider {
    width: calc(100% - 40px);
    display: block;
    margin: 0 auto;
  }
}

.partenter__slider .slick-arrow {
  width: 15px;
  height: 15px;
  font-size: 0px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  outline: 0;
  cursor: pointer;
  opacity: .5;
  transition: .2s;
}

.partenter__slider .slick-arrow:hover {
  opacity: 1;
}

.partenter__slider .slick-prev {
  left: -15px;
  background: url("../ico/go-back-left-arrow.svg") center center no-repeat;
  background-size: 15px 15px;
  border: 0px;
}

.partenter__slider .slick-next {
  right: -15px;
  background: url("../ico/right-arrow-forward.svg") center center no-repeat;
  background-size: 15px 15px;
  border: 0px;
}

.partenter__slider-item {
  display: flex !important;
  align-items: center;
  justify-content: center;
  height: 70px;
}

.partenter__slider-item img {
  opacity: .3;
}

.partenter__slider .slick-dots {
  width: 100%;
  height: 5px;
  display: flex;
  justify-content: space-between;
  margin-top: 23px;
}

@media (max-width: 768px) {
  .partenter__slider .slick-dots {
    margin-top: 0px;
  }
}

.partenter__slider .slick-dots li {
  flex: 1;
}

.partenter__slider .slick-dots li.slick-active button {
  opacity: 1;
}

.partenter__slider .slick-dots li button {
  width: 100%;
  height: 100%;
  font-size: 0px;
  border: 0px;
  background-color: transparent;
  outline: 0px;
  background-color: var(--partenter-dots-bg);
  opacity: .2;
  transition: .2s;
}

